.news-card {
    border: 1px solid #ddd;
    border-radius: 8px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    max-width: 500px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    margin-bottom: 16px;
    width: 500px;
  }
  
  .news-card-image {
    width: 100%;
    /* height: 200px; */
    object-fit: cover;
  }
  
  .news-card-content {
    padding: 16px;
  }
  
  .news-card-heading {
    font-size: 1.2em;
    margin: 0;
    color: #333;
  }
  
  .news-card-subheading {
    font-size: .8em;
    color: #666;
    margin: 8px 0;
  }
  
  .news-card-meta {
    display: flex;
    justify-content: space-between;
    font-size: 0.8em;
    color: #999;
    margin-bottom: 16px;
  }
  
  .news-card-read-more {
    text-decoration: none;
    color: #007bff;
    font-weight: bold;
  }
  .news-card-source{
    font-size: 1.2em;
    color: #007bff;
    padding: 0px;
  }
  .news-detail{
    max-width: 900px;
  }
  .news-detail-page{
    max-width: fit-content;
    margin-left: auto;
    margin-right: auto;
  }
 
  .source-image{
    display: inline-block;
    height: 40px;
    padding-bottom: 10px;
    margin-right: 7px;
  }